import { AbstractErrorHandler, ErrorData } from '@engine/crud';

export enum CONTROLLER_ATTR_ERROR_CODE {
    SAME_ORIGIN_ITEM = '11300207',
    HAVE_RING = '11300208'
}

export class ControllerAttrErrorHandler extends AbstractErrorHandler {
    public handle(data: ErrorData) {
        BI.Msg.toast(data?.errorMsg);
        throw null;
    }
}
